.App {
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;

}

.grid {
  margin-top: 30px;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  max-width: 1200px;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 30px;
}

.LeftColumn {
  display: -ms-flexbox;
  display: flex;
  width: 250px;
  padding: 30px;
  -ms-flex-align: baseline !important;
      align-items: baseline !important;;
}

.center {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
}

@media (min-width: 768px) and (max-width: 979px) {
  .LeftColumn {
    width: 150px;
    padding: 10px;
  }
  .grid {
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
}

@media (max-width: 480px) {
  .App {
    display: block;
  }
  .grid {
    margin: 0;
    padding-bottom: 15px;
  }
  .LeftColumn {
    margin: auto;
    padding: 5px;
  }
}



